<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <!-- Page Content -->
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid">
        <div class="d-flex flex-row-reverse">
          <router-link
            :to="{ name: 'AddUser' }"
            class="btn btn-sm btn-primary m-1"
          >
            <i class="fas fa-plus"></i>
            کاربر جدید
          </router-link>

          <button class="btn btn-sm btn-danger m-1" @click="deleteUsers()">
            <i class="fas fa-trash"></i>
            حذف
          </button>
        </div>
        <div class="my-1 row text-start">
          <div class="col-md-6 col-sm-4 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                placeholder="نام یا نام خانوادگی"
                v-model="formSearch.name"
                v-on:keyup.enter="startSearch()"
              />
              <label for="name">جستجو نام یا نام خانوادگی *</label>
            </div>
          </div>
          <div class="col-md-6 col-sm-4 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                placeholder="تلفن همراه"
                v-model="formSearch.email"
                @keyup.enter="startSearch()"
              />
              <label>جستجو تلفن همراه *</label>
            </div>
          </div>
          <div class="col-md-2 col-sm-12 col-xs-12">
            <a class="btn btn-primary btn-block w-100" @click="startSearch()">
              جستجو
            </a>
            <span v-show="showSpin">
              <i class="fas fa-spinner fa-spin mt-3"></i>
            </span>
          </div>
          <div class="col-md-2 col-sm-12 col-xs-12">
            <a class="btn btn-primary btn-block w-100" @click="resetPage()">
              پاکسازی
            </a>
          </div>
        </div>
        <div class="table-responsive">
          <table class="caption-top table table-hover table-striped">
            <caption>
              فهرست کاربران
            </caption>
            <thead class="table-dark">
              <tr>
                <th scope="col">
                  <input
                    type="checkbox"
                    id="checkAll"
                    @click="checkAllItems()"
                  />
                </th>
                <th scope="col">ردیف</th>
                <th scope="col">نام</th>
                <th scope="col">نام خانوادگی</th>
                <th scope="col">تلفن همراه</th>
                <th scope="col">معرف</th>
                <th scope="col">گروه کاربری</th>
                <th scope="col">شناسه</th>
                <th scope="col">ثبت نام</th>
                <th scope="col">ویرایش</th>
                <th scope="col">صدور فاکتور</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in usersIndex" :key="item.id">
                <td style="width: 3%">
                  <input
                    type="checkbox"
                    :id="`ch-${item.id}`"
                    :checked="checkAll"
                    @change="toggleCheckedArr(item.id)"
                  />
                </td>
                <th style="width: 3%" scope="row">
                  {{ index + 1 }}
                </th>
                <td>
                  {{ item.first_name }}
                </td>
                <td>
                  {{ item.last_name }}
                </td>
                <td>
                  {{ item.email }}
                </td>
                <td>
                  {{
                    item.referrer_user
                      ? item.referrer_user.first_name +
                        " " +
                        item.referrer_user.last_name
                      : "--"
                  }}
                </td>
                <td>
                  {{ item.group.name }}
                </td>
                <td style="width: 3%">
                  {{ item.id }}
                </td>
                <td>
                  {{ this.utils.toJalali(item.created_at) }}
                </td>
                <td>
                  <router-link
                    :to="{ name: 'UserEdit', params: { userId: item.id } }"
                    class="btn btn-success btn-sm"
                  >
                    <i class="fas fa-edit"></i>
                    ویرایش
                  </router-link>
                </td>
                <td>
                  <router-link
                    :to="{
                      name: 'AddOrder',
                      params: {
                        userId: item.id,
                        userFullName:
                          (item.first_name ? item.first_name : '') +
                          ' ' +
                          item.last_name,
                      },
                    }"
                    class="btn btn-primary btn-sm"
                  >
                    <i class="fas fa-edit"></i>
                    صدور فاکتور
                  </router-link>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div
          v-if="lastPage > 1"
          class="d-flex justify-content-center bd-highlight mb-3"
        >
          <span
            v-if="showPreviusSpin"
            class="fas fa-spinner fa-spin mt-3"
          ></span>
          <a
            :disabled="currentPage > 1"
            :class="
              'btn btn-primary btn-sm m-1 ' +
              (currentPage > 1 ? '' : 'disabled')
            "
            @click="previusPage()"
          >
            قبلی
          </a>
          <select class="m-1 p-1" @change="changePage($event)">
            <option
              v-for="index in this.lastPage"
              :key="index"
              :selected="index == this.currentPage"
            >
              {{ index }}
            </option>
          </select>
          <a
            :disabled="currentPage != lastPage"
            :class="
              'btn btn-primary btn-sm m-1 ' +
              (currentPage != lastPage ? '' : 'disabled')
            "
            @click="nextPage()"
          >
            بعدی
          </a>
          <span v-if="showNextSpin" class="fas fa-spinner fa-spin mt-3"></span>
        </div>
      </div>
    </div>
    <!-- /#page-content-wrapper -->
  </div>
  <!-- /#wrapper -->
</template>

<script>
import TheSidemenu from "../components/TheSidemenu";
import TheTopmenu from "../components/TheTopmenu";
import ArefApi from "../libs/api.js";
import utils from "../libs/utils";

export default {
  created() {
    this.utils = new utils();
    this.arefApi = new ArefApi();
  },
  name: "ListUser",
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      userList: null,
      usersIndex: null,
      checkAll: false,
      checked: [],
      currentPage: this.$route.params.page ? this.$route.params.page : 1,
      lastPage: null,
      showPreviusSpin: false,
      showNextSpin: false,
      formSearch: {
        name: null,
        email: null,
        page: 1,
      },
      showSpin: false,
      searchMod: false,
    };
  },
  methods: {
    async getUsersIndex(page) {
      this.arefApi
        .getUsersIndex(page)
        .then((usersIndex) => {
          this.usersIndex = usersIndex.data;
          this.currentPage = usersIndex.meta.current_page;
          this.lastPage = usersIndex.meta.last_page;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    deleteUsers() {
      if (!this.checked.length) {
        this.$swal.fire({
          title: "اخطار",
          text: "هیچ موردی جهت حذف انتخاب نشده است",
          icon: "warning",
        });
        return;
      }

      this.$swal
        .fire({
          title: "اخطار حذف",
          text: "آیا حذف انجام شود؟",
          icon: "warning",
          showCancelButton: true,
          cancelButtonText: "خیر",
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "بله حذف انجام شود!",
        })
        .then((value) => {
          if (value.isConfirmed) {
            const deleteItems = {
              ids: this.checked,
            };
            this.arefApi
              .bulkDeleteUser(deleteItems)
              .then(() => {
                this.$swal
                  .fire({
                    title: "",
                    text: "حذف با موفقیت انجام شد",
                    icon: "success",
                    confirmButtonText: "متوجه شدم",
                  })
                  .then(() => {
                    this.getUsersIndex(this.currentPage);
                  });
              })
              .catch(() => {
                this.$swal.fire({
                  title: "اخطار",
                  text: "خطا در حذف ",
                  icon: "warning",
                  confirmButtonText: "متوجه شدم",
                });
              });
          }
        });
    },
    startSearch() {
      if (!this.formSearch.name && !this.formSearch.email) {
        return;
      }
      this.searchMod = true;
      this.formSearch.page = 1;
      this.seachUser(this.formSearch);
    },
    seachUser(formSearch) {
      this.showSpin = true;
      this.arefApi
        .searchUser(formSearch)
        .then((usersIndex) => {
          this.usersIndex = usersIndex.data;
          this.currentPage = usersIndex.meta.current_page;
          this.lastPage = usersIndex.meta.last_page;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        })
        .finally(() => {
          this.showSpin = false;
        });
    },
    resetPage() {
      this.formSearch.name = null;
      this.formSearch.email = null;
      this.searchMod = false;
      this.currentPage = 1;
      this.getUsersIndex(this.currentPage);
    },
    checkAllItems: function () {
      if (!this.checkAll) {
        this.checkAll = true;
        for (let i in this.usersIndex) {
          this.checked.push(this.usersIndex[i].id);
        }
      } else {
        this.checkAll = false;
        this.checked = [];
      }
    },
    toggleCheckedArr: function (itemId) {
      if (this.checked.indexOf(itemId) >= 0) {
        this.checked.splice(this.checked.indexOf(itemId), 1);
      } else {
        this.checked.push(itemId);
      }
    },
    nextPage: async function () {
      this.showNextSpin = true;
      this.currentPage++;
      this.$router.push(`/admin/listusers/${this.currentPage}`);
      if (this.searchMod) {
        this.formSearch.page = this.currentPage;
        await this.seachUser(this.formSearch);
        this.showNextSpin = false;
        return;
      }

      await this.getUsersIndex(this.currentPage);
      this.showNextSpin = false;
    },
    previusPage: async function () {
      this.showNextSpin = true;
      this.currentPage--;
      this.$router.push(`/admin/listusers/${this.currentPage}`);
      if (this.searchMod) {
        this.formSearch.page = this.currentPage;
        await this.seachUser(this.formSearch);
        this.showNextSpin = false;
        return;
      }

      await this.getUsersIndex(this.currentPage);
      this.showNextSpin = false;
    },
    changePage: function (event) {
      this.$router.push(`/admin/listusers/${event.target.value}`);
      this.getUsersIndex(event.target.value);
    },
  },
  mounted() {
    if (this.searchMod) {
      this.seachUser(this.formSearch);
      return;
    }
    this.getUsersIndex(this.currentPage);
  },
};
</script>

